import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';
import { Watch } from 'vue-property-decorator';

import bottomSheet from 'muse-ui/bottomSheet';
import floatButton from 'muse-ui/floatButton';

import { triggerAsyncId } from 'async_hooks';
import { fail } from 'assert';
import { setTimeout } from 'timers';
import Api from '~/api';
import { ToastMsg } from '~/entity/toast';
import Utils from '~/utils';
import { Ipage, IlocalPage } from '~/entity/page';
import { Iletter } from '~/entity/task';
import {
    BOTTOM_SHEET, MODAL_0, ROOT_PATH, titlePre2, titlePre1, sharedesc1, LOADING_TEXT, titlePre3, addFriendInShare,
    INVITE_ACTION_GROUP, TITLE_MATCHMAKER_INVITE, SHARE_DESC_1, SHARE_DESC_2, TITLE_FRIEND_INVITE, INVITE_ACTION, SEND_MSG_HINT, ADD_FRIEND_HINT, FIND_MATCHMAKER_HINT, NOT_SEFL_HELP
} from '~/config';
import { IUserInterface } from '~/entity/user';
import { IGestureEvent } from '~/directives';
import { CACHE_NAME } from '~/cache';
import { IPersonalParams, IAcquaintanceTaskParams } from 'src/entity/route_params';
// 引入测试数据
// import fakeNewletters from './fake/newletters.json';
// const fakeNewletters = require('./fake/newletters.json');

// 找红娘列表对象接口
interface IMatchMaker {
    // 红娘评分
    grade: number;
    headPortrait: string;
    level: number;
    // 成功牵线对数
    matchNum: number;
    // 红娘定价，以分为单位
    price: number;
    realname: string;
    // 单身好友数量
    singleNum: number;
    // 红娘uid
    uid: string;
}

@Component({
    components: {
        'mu-bottom-sheet': bottomSheet,
        'mu-float-button': floatButton
    }
})
export default class AiComp extends Vue {

    pageSize: number = 10;

    loadingText = LOADING_TEXT;

    @Action('showToast')
    showToast;

    @Action('updateTabValue')
    updateTabValue;

    @State(state => state.user.user)
    myInfo: IUserInterface;

    @State(state => state.user.roles)
    userRoles;

    /* 滑动tab配置 */
    swiperOpt: any = new class {
        on: any = {};
        touchAngle = 20;
        iOSEdgeSwipeDetection = true;
        shortSwipes = true;
        constructor(public superThis: AiComp) {
            this.on.slideChangeTransitionEnd = function () {
                // superThis.initWxByTabIndex('' + this.activeIndex, false); // 初始化分享内容   
                superThis.changeTab('' + this.activeIndex, false);
                superThis.setWXShareByTab(this.activeIndex + '');
            };
        }
    }(this);

    activeTabIndex: string = '0';

    /* 推荐信数据 */
    letterListContainer: HTMLElement = null;

    refreshingLetter = false;

    loadingLetter = false;

    letterPage: IlocalPage = {
        hasMore: false,
        curPage: 1
    };

    letterList: Iletter[] = [];

    // selectedLetter: Iletter = null;
    selectedLetterIndex: number = null;

    /* 红娘榜数据 */
    matchMakerListContainer: HTMLElement = null;

    nonSingleMatchMakerListContainer: HTMLElement = null;

    refreshingMatchMaker = false;

    loadingMatchMaker = false;

    matchMakerPage: IlocalPage = {
        hasMore: false,
        curPage: 1
    };

    matchMakerList: IMatchMaker[] = [];

    /* 新推荐信，浮动框 */
    newLetters: Iletter[] = [];

    /* 底部action sheet */
    @Action('addModal')
    addModal;

    @Action('hideModal')
    hideModal;

    isActiveModal = false;

    @State(state => state.modal.BOTTOM_SHEET)
    bottomSheet: boolean;

    @State(state => state.acquaintanceState.acquaintanceList)
    acquaintanceList;

    @Action('resetFriendList')
    resetFriendList;

    @Action('resetSelectedAcquaintanceList')
    resetSelectedAcquaintanceList;

    $refs: {
        aiSwiper: any,
        nonSingleMatchMakerListContainer: HTMLElement,
        matchMakerListContainer: HTMLElement,
        letterListContainer: HTMLElement
    };

    get swiper() {
        return this.$refs.aiSwiper.swiper;
    }

    /* 分享配置 */
    letterInviteCode: string = null;

    @State(state => state.inviteActions)
    inviteActions: string[];

    @Action('removeInviteAction')
    removeInviteAction;

    // 引导页状态
    @State(state => state.guideStatus)
    guideStatus: string;

    @State(state => state.user.matchDisplay)
    matchDisplay;

    @Action('updateGuideStatus')
    updateGuideStatus;

    @Watch('inviteActions')
    async changeIndex(newVal, oldVal) {
        const index = this.inviteActions.indexOf(INVITE_ACTION.CHANGE_INDEX);
        if (index === -1) return;

        await Utils.initedUser();
        if (this.myInfo.loveState === 0) {
            // 单身跳转到悬赏榜
            this.changeTab('1', true);
            this.removeInviteAction(INVITE_ACTION.CHANGE_INDEX);
        }
    }

    get isAuthorized() {
        return this.userRoles.indexOf('authorized_user') !== -1;
    }

    // 显示红娘榜认证提示
    get showMatchMakterListDialog() {
        if (this.guideStatus && this.guideStatus[0] === '0' && this.matchDisplay === 1) {
            return true;
        } else {
            return false;
        }
    }

    // matchMakerInviteCode: string = null;
    created() {
        console.log('created');
    }

    async activated() {
        this.updateTabValue('ai');
        this.isActiveModal = true;
        await Utils.initedUser();
        if (this.myInfo.loveState === 1) {
            this.activeTabIndex = '0';
            this.nonSingleMatchMakerListContainer = this.$refs.nonSingleMatchMakerListContainer;
        } else {
            this.letterListContainer = this.$refs.letterListContainer;
            this.matchMakerListContainer = this.$refs.matchMakerListContainer;
        }
        if (this.myInfo.loveState === 0) {
            this.refreshLetter();
        }
        // 刷新红娘列表
        this.refreshMatchMaker();

        // 配置微信分享
        this.initWX();
    }

    deactivated() {
        this.isActiveModal = false;
        console.log('deactivated');
        if (this.bottomSheet) {
            this.closeBottomSheet();
        }
    }

    /***************初始化微信分享******************/
    initWX() {
        // 初始化微信jssdk 以及 获取推荐信tab邀请码
        this.getLetterInviteCode();
        this.setWXShareByTab(this.activeTabIndex);
    }

    async getLetterInviteCode() {
        this.letterInviteCode = await Utils.initWX(INVITE_ACTION_GROUP.CHANGE_AND_ADD);
    }

    // async getMatchMakerInviteCode() {
    //     this.matchMakerInviteCode = await Utils.initWX();
    // }

    setWXShareByTab(tabVal: string) {
        let url: string;
        let title: string;
        let desc: string;
        let imgUrl: string;
        if (tabVal === '0') {
            // 红娘榜
            url = Utils.getShareUrl('/tab/ai');
            title = TITLE_MATCHMAKER_INVITE + this.myInfo.realname;
            desc = SHARE_DESC_2;
            imgUrl = Utils.getThumbnailImg(this.myInfo.headPortrait);
        } else {
            // 推荐信
            url = Utils.getShareUrl('/tab/ai', this.letterInviteCode);
            title = TITLE_FRIEND_INVITE + this.myInfo.realname;
            desc = SHARE_DESC_1;
            imgUrl = Utils.getThumbnailImg(this.myInfo.headPortrait);
        }
        Utils.setWXShareConfig(url, title, desc, imgUrl);
    }

    changeTab(val: string, isSlide: boolean = true) {
        this.activeTabIndex = val;
        console.log(`changeTab: ${this.activeTabIndex}`);
        if (isSlide) {
            this.swiper.slideTo(parseInt(val, 10));
        }
        // 切换分享配置
        // this.setWXShareByTab(val);
    }

    /**
     * 刷新推荐信
     */
    async refreshLetter() {
        this.refreshingLetter = true;
        try {
            // 先 刷新 新推荐信
            const res1 = await Api.getNewLetters();
            const newLetters: Iletter[] = res1.body.obj;

            this.newLetters = newLetters;

            // this.newLetters = fakeNewletters.obj.rows;

            // 再 刷新 推荐信列表
            const res2 = await Api.getLetters(1, this.pageSize);
            const letters: Iletter[] = res2.body.obj.rows;
            this.letterList = letters;

            const page: Ipage = res2.body.obj.page;
            Utils.checkPage(this.letterPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.refreshingLetter = false;
        }
    }

    /**
     * 加载更多推荐信
     */
    async loadMoreLetter() {
        this.loadingLetter = true;
        try {
            const res = await Api.getLetters(this.letterPage.curPage + 1, this.pageSize);
            const letters: Iletter[] = res.body.obj.rows;
            this.letterList = this.letterList.concat(letters);

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.letterPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
            this.letterPage.hasMore = false;
        } finally {
            this.loadingLetter = false;
        }
    }

    /**
     * 刷新找红娘
     */
    async refreshMatchMaker() {
        this.refreshingMatchMaker = true;
        try {
            // 刷新红娘列表
            const res = await Api.getMatchMakers(1, this.pageSize);
            this.matchMakerList = res.body.obj.rows;

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.matchMakerPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.refreshingMatchMaker = false;
        }
    }

    /**
     * 加载更多红娘列表
     */
    async loadMoreMatchMaker() {
        this.loadingMatchMaker = true;
        try {
            const res = await Api.getMatchMakers(this.matchMakerPage.curPage + 1, this.pageSize);
            const matchMakers: IMatchMaker[] = res.body.obj.rows;
            this.matchMakerList = this.matchMakerList.concat(matchMakers);

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.matchMakerPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
            this.matchMakerPage.hasMore = false;
        } finally {
            this.loadingMatchMaker = false;
        }
    }

    scoreTrans(score) {
        const socreInt = Math.round(score);
        const starArray = [true, true, true, true, true];
        for (let i = 0; i < 5; i++) {
            starArray[i] = i < socreInt;
        }
        return starArray;
    }
    /**
     * 点击推荐信列表项
     * @param event HTML点击事件
     */
    async clickLetter(event: Event, index: number, letter: Iletter) {
        const target = <HTMLElement>event.target;
        const action = target.getAttribute('data-action');
        if (action === 'actionSheet') {
            console.log(index);
            this.selectedLetterIndex = index;
            // this.selectedLetter = letter;
            this.addModal(BOTTOM_SHEET);

        } else if (action === 'addfriend') {
            if (letter.alreadyFriend === 0) {
                // 加好友
                try {
                    await Api.addFriendByLetterId(letter.id);
                    this.showToast(new ToastMsg(ADD_FRIEND_HINT));
                } catch (e) {
                    this.showToast(new ToastMsg(e.message));
                }
            } else {
                this.showToast(new ToastMsg(SEND_MSG_HINT));
            }

        } else {

            const id = this.letterList[index].id;
            Utils.saveItem(CACHE_NAME.letter + id, this.letterList[index]);
            // console.log('进入主页');
            const params: IPersonalParams = {
                action: 'recommendation',
                id
            };
            this['$router'].push({
                name: 'personalPage',
                params
            });
        }
    }

    clickMatchMaker(event: Event, matchMaker: IMatchMaker) {
        const target = <HTMLElement>event.target;
        const action = target.getAttribute('data-action');
        // 需要增加判断用户角色,是否已认证用户
        if (action === 'findObject') {
            if (!this.myInfo || this.myInfo.loveState === 1) {
                this.showToast(new ToastMsg(FIND_MATCHMAKER_HINT));
                return;
            }

            if (this.myInfo.uid === matchMaker.uid) {
                this.showToast(new ToastMsg(NOT_SEFL_HELP));
                return;
            }

            // ******暂时不做认证限制*******
            // this.resetFriendList(Utils.cloneObj(this.friendsList));
            // if (this.userRoles.indexOf('authorized_user') !== -1) { // 判断用户角色,是否已认证用户
            this.resetSelectedAcquaintanceList([
                {
                    toUid: matchMaker.uid,
                    realname: matchMaker.realname,
                    amount: matchMaker.price,
                    headPortrait: matchMaker.headPortrait
                }
            ]);
            const params: IAcquaintanceTaskParams = {
                action: 'fromFindMatchMaker',
                id: matchMaker.uid
            };
            this['$router'].push({
                name: 'acquaintanceTask',
                params
            });
            // } else {
            //     this.showToast(new ToastMsg('您未认证，暂时无法非好友红娘帮忙找对象，请先完成认证'));
            //     this['$router'].push({
            //         name: 'authIndex'
            //     });
            // }           
        } else {
            // 进入红娘主页
            const params: IPersonalParams = {
                action: 'findMatchmaker',
                id: matchMaker.uid
            };
            this['$router'].push({
                name: 'personalPage',
                params
            });
        }
    }

    // 进入认证页面
    // enterAuthIndex() {
    //     this['$router'].push({
    //         name: 'authIndex'
    //     });
    // }

    /**
     * 关闭bottom sheet
     */
    closeBottomSheet() {
        // history.back();
        if (this.bottomSheet) {
            this.hideModal();
        }
    }

    /**
     * 点击底部栏
     * @param item 
     */
    async clickSheetItem(item) {
        if (item.title === '不再推荐') {
            try {
                const res = await Api.noMoreRecommend(this.letterList[this.selectedLetterIndex].id);
                this.letterList.splice(this.selectedLetterIndex, 1);
                // history.back();
                this.closeBottomSheet();
            } catch (e) {
                this.showToast(new ToastMsg(e.message));
            }
        }
    }

    /**
     * 处理浮动框拖拽
     * @param event 
     * @param params 
     */
    async dragHandler(event: IGestureEvent, params) {
        console.log('touch时间');
        const target = <HTMLElement>event.currentTarget;
        const gesture = event.gesture;
        // if (!gesture.isRelease) {
        //     // 正在拖拽
        //     target.style.cssText = 'transform: translate3d(' + gesture.deltaX + 'px,' + gesture.deltaY + 'px, 0);' +
        //         '-webkit-transform: translate3d(' + gesture.deltaX + 'px,' + gesture.deltaY + 'px, 0);';
        //     return;
        // }

        // 释放拖拽
        if (gesture.speed > 0.4 || Math.abs(gesture.deltaX) > 300) {
            let direction: string;
            gesture.deltaX > 0 ? direction = 'right' : direction = 'left';

            target.setAttribute('direction', direction);
            target.setAttribute('deltaY', '' + gesture.deltaY);

            // 清除推荐用户
            const letter: Iletter = params.letter;
            this.newLetters.shift();
            if (direction === 'right') {
                if (letter.alreadyFriend === 0) {
                    // 发出加好友请求
                    try {
                        await Api.addFriendByLetterId(letter.id);
                        this.showToast(new ToastMsg(ADD_FRIEND_HINT));
                    } catch (e) {
                        this.showToast(new ToastMsg(e.message));
                    }
                } else {
                    // 提示用户对方已经是您的好友
                    this.showToast(new ToastMsg('对方已经是您的好友'));
                }
            }
        } else {
            target.style.cssText = 'transform: translate3d(0px, 0px, 0); transition: transform 0.5s;' +
                '-webkit-transform: translate3d(0px, 0px, 0); -webkit-transition: transform 0.5s;';
        }
    }

    /**
     * 点击新推荐信 的忽略或者添加好友
     * @param event 
     * @param letter 
     */
    async clickNewLetter(event: Event, letter: Iletter) {
        console.log('点击事件');
        const target = <HTMLElement>event.target;
        const currentTarget = <HTMLElement>event.currentTarget;
        const action = target.getAttribute('data-action');

        if (action === 'ignore') {
            currentTarget.setAttribute('direction', 'left');
            this.newLetters.shift();
        } else if (action === 'add-friend') {
            currentTarget.setAttribute('direction', 'right');
            if (letter.alreadyFriend === 0) {
                // 发出加好友请求
                try {
                    await Api.addFriendByLetterId(letter.id);
                    this.showToast(new ToastMsg(ADD_FRIEND_HINT));
                } catch (e) {
                    this.showToast(new ToastMsg(e.message));
                }
            } else {
                // 提示用户对方已经是您的好友
                this.showToast(new ToastMsg('对方已经是您的好友'));
            }
            this.newLetters.shift();
        }
    }

    enterMyInfo() {
        Utils.saveItem(CACHE_NAME.updateUser, this.myInfo);
        const params = {
            action: 'me',
            id: this.myInfo.uid,
            index: '1'
        };
        this['$router'].push({
            name: 'personalInfo',
            params
        });
    }

    animateBeforeLeave(el: HTMLElement) {
        console.log('leave');
        const deltaY = el.getAttribute('deltaY') || 0;
        const direction = el.getAttribute('direction') || 'right';
        if (direction === 'right') {
            el.style.cssText = 'transform: translate3d(1000px,' + deltaY + 'px, 0); transition: transform 1s;' +
                '-webkit-transform: translate3d(1000px,' + deltaY + 'px, 0); -webkit-transition: transform 1s;z-index:3';
        } else {
            el.style.cssText = 'transform: translate3d(-1000px,' + deltaY + 'px, 0); transition: transform 1s;' +
                '-webkit-transform: translate3d(-1000px,' + deltaY + 'px, 0); -webkit-transition: transform 1s;z-index:3';
        }
    }

    /* 红娘认证提示框认证 */
    hideMatchMakterListDialog() {
        this.updateGuideStatus({
            index: 0,
            value: 1
        });
    }

    async cancelMatchMakterListDialog() {
        this.updateGuideStatus({
            index: 0,
            value: 1
        });
        // 提交后台状态更新
        try {
            const res = await Api.updateGuideStatus(this.guideStatus);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }

    // 进入认证界面
    async confirmMatchMakterListDialog() {
        this.updateGuideStatus({
            index: 0,
            value: 1
        });
        // 跳转去认证
        this.$router.push({
            name: 'authIndex'
        });
        try {
            const res = await Api.updateGuideStatus(this.guideStatus);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }
}



